@font-face {
    font-family: alphaDeath;
    src: url(./fonts/AlphaDeathPersonalUseGrunge-LzAE.ttf);
}
@font-face {
    font-family: gipsyDanger;
    src: url(./fonts/GipsyDangerPersonalUse-8W8z.ttf);
}

@font-face {
  font-family: universalKnowledge;
  src: url(./fonts/Universal\ Knowledge.ttf);
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background-color: rgb(26, 26, 26);
}



.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
}